*{
    margin: 0px;
    padding: 0px;
}
section{
    width: 80%;
    margin: auto;
    margin-top: 5%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
}

.cards{
    width: 250px;
    padding: 20px;
    display: flex;
}
.container{
    margin: auto;
}
.cards:hover{
    box-shadow: 0 10px 10px 10px rgba(0,0,0,0.1);
    cursor: pointer;
}
.img_box img{
    width: 150px;
    height: 200px;
}
.details p:nth-child(1){
    font-weight: bold;
}
.details p:nth-child(2){
    color: grey;
    font-size: 0.8rem;
}
.details p:nth-child(3){
    font-weight: bold;
    color: rgb(7, 196, 4);
}
.details button{
    padding: 5px 10px;
    border: none;
    font-weight: bold;
    background-color: var(--primary-color);
    outline: none;
}